import React from 'react'
import SideNavLink from './SideNavLink'

class SideNav extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            links: [
                {
                    'location': '#',
                    'icon': 'question',
                    'label': 'Server Information',
                    'children':
                    [
                        {'location': '/about', 'label': 'About Us'},
                        {'location': '/rules', 'label': 'Rules'},
                        {'location': '/join', 'label': 'Join'},
                        {'location': '/ranks', 'label': 'Player Ranks'},
                        {'location': '/detailed-status', 'label': 'Detailed Status'}
                    ]
                },
                {
                    'location': '#',
                    'icon': 'line-chart',
                    'label': 'Game Stats',
                    'children': [
                        {'location': '/stats/server', 'label': 'Server'},
                        {'location': '/stats/players', 'label': 'Players'},
                    ]
                },
                {
                    'location': '/map',
                    'icon': 'map-marker',
                    'label': 'Live Map',
                },
                {
                    'location': '/forums',
                    'icon': 'comments',
                    'label': 'Forums'
                },

                {
                    'location': '/gallery',
                    'icon': 'photo',
                    'label': 'Gallery'
                },
                /*{
                    'location': '#',
                    'icon': 'cogs',
                    'label': 'Mod/Admin Tools',
                    'children':
                    [
                        {'location': '/prism','label': 'Prism Lookup'},
                        {'location': '/notebook', 'label': 'Player Notes'},
                        {'location': '/tickets', 'label': 'Support Tickets'},
                        {'location': '/name-lookup', 'label': 'Name Lookup'}
                    ]
                },
                {
                    'location': '#',
                    'icon': 'database',
                    'label': 'Website Tools',
                    'children':
                    [
                        {'location': '/blog', 'label': 'Articles'},
                        {'location': '/rules', 'label': 'Rules'},
                    ]
                }*/
            ]
        }
    }

    render() {
        var linkNodes = this.state.links.map(function(link) {
            return (
                    <SideNavLink link={link} key={link.label} />
            );
        });
        return (
            <li>
                <ul className='collapsible sideNavCollapsible' data-collapsible="accordion">
                    {linkNodes}
                </ul>
            </li>
        )
    }
}

export default SideNav;



