import React from 'react'
import SubSideNavLink from './SubSideNavLink'

class SubSideNavLinkList extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        var subNodes = this.props.links.map(function(link) {
            return (
                <SubSideNavLink link={link} key={link.label}/>
            );
        })
        return (
            <ul>
                {subNodes}
            </ul>
        );
    }
}

export default SubSideNavLinkList;



