import React from 'react'
import { Link } from 'react-router'
import TopNav from './TopNav'
import Hamburger from './Hamburger'
import OnlinePlayersWidget from '../../components/Widgets/OnlinePlayers/OnlinePlayersWidget'
import ServerStatusesWidget from '../../components/Widgets/ServerStatuses/ServerStatusesWidget'
import SideNav from '../navigation/SideNav'

class Header extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <header>
                <TopNav/>
                <Hamburger/>
                <div className='outer-nav'>
                    <ul id='slide-out' className='side-nav fixed'>
                        <li className='sidenav-section'><Link to='/' className='brand-logo' id='logo-container'><img className='responsive-img' src='//static.moosecraft.us/img/logo.png' alt='Moosecraft' /></Link></li>
                        <li className='sidenav-section'>
                            <img src='//static.moosecraft.us/img/avatar.png' alt="Avatar" className='responsive-img' id='avatar' />
                            <h6>Welcome, Guest</h6>
                            <a href='#' className='btn white-text waves-effect waves-light brown lighten-1' id='signin'>Login</a>
                            <a href='#' className='btn white-text waves-effect waves-light brown lighten-1' id='register'>Register</a>
                        </li>
                        <OnlinePlayersWidget />
                        <ServerStatusesWidget />
                        <SideNav />
                    </ul>
                </div>
            </header>
        );
    }
}

export default Header;



