import React from 'react'

class ServerRule extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            date_difference: 0
        }
    }

    render() {
        var badge;

        if (this.state.date_difference >= 30) {
            badge = <span></span>
        } else {
            badge = <span className='new badge'></span>
        }

        return (
            <li>
                <div className='collapsible-header'><span className='bold'>{this.props.name}</span>{badge}</div>
                <div className='collapsible-body'><p>{this.props.description}</p></div>
            </li>
        )
    }

    mysqlTimeStampToDate(timestamp) {
        //function parses mysql datetime string and returns javascript Date object
        //input has to be in this format: 2007-06-05 15:26:02
        //var regex=/^([0-9]{2,4})-([0-1][0-9])-([0-3][0-9]) (?:([0-2][0-9]):([0-5][0-9]):([0-5][0-9]))?$/;
        var regex=/^([0-9]{2,4})-([0-1][0-9])-([0-3][0-9])T([0-2][0-9]):([0-2][0-9]):([0-2][0-9])(.....)*/;
        var parts=timestamp.replace(regex,"$1 $2 $3 $4 $5 $6 $7").split(' ');
        return new Date(parts[0],parts[1]-1,parts[2],parts[3],parts[4],parts[5]);
    }

    componentDidMount() {
        var date = this.mysqlTimeStampToDate(this.props.created);
        var difference = Date.now() - date;
        var days = Math.round(difference / (1000*60*60*24));
        this.setState({
            date_difference: days
        });
    }
}

export default ServerRule;



