import React from 'react'
import moment from 'moment'
import _ from 'lodash'
import Helmet from 'react-helmet'
import { Link } from 'react-router'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'

function calculatePlayTime(playertime) {
    var total_time = 0;

    // loop to get Playtime
    for (var key in playertime) {
        total_time = total_time + playertime[key]['value'];
    }

    if (total_time != 0) {
        var outputString = '';

        // calculate our how many days, hours, minutes player has played
        var days = Math.floor(total_time/60/60/24);
        total_time = total_time - (days*60*60*24);

        if (days != 0) {
            outputString += days + 'd';
        }

        var hours = Math.floor(total_time/60/60);
        total_time = total_time - (hours*60*60);

        if ((days == 0 && hours != 0) || days != 0) {
            outputString += hours + 'h';
        }

        var minutes = Math.floor(total_time/60);
        var seconds = total_time - (minutes*60);
        outputString += minutes + 'm';

        // we aren't going to show seconds if playtime is over 10 hours
        if (days < 1 && hours < 10) {
            outputString += seconds + 's';
        }
    } else {
        outputString = '-- N/A --'
    }

    return outputString;
}

function calculateDateTime(time) {
    for (var key in time) {
        return time[key]['value'];
    }
}

class StatsPlayerIndex extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            players: []
        }
    }

    componentDidMount() {
        this.loadPlayers();
        setInterval(this.loadPlayers, 300000);
    }

    componentWillUnmount() {
        this.serverRequest.abort();
        clearInterval(this.loadPlayers);
    }

    loadPlayers() {
        this.serverRequest = $.ajax({
            url: '//api.moosecraft.us/api/players',
            dataType: 'json',
            cache: false,
            success: function(data) {
                this.setState({
                    players: data['players']
                });
                console.log(this.state.players);
            }.bind(this),
            error: function(xhr, status, err) {
                console.error('//api.moosecraft.us/api/players', status, err.toString());
            }.bind(this)
        });
    }

    render() {
        var newArr = _.sortBy(this.state.players, 'stats_playtime', function(n) {
            return Math.sin(n);
        });
        console.log(newArr);

        var playerNodes = this.state.players.map(function(player) {
            var formattedTime = calculatePlayTime(player.stats_playtime);
            var lastJoin = calculateDateTime(player.stats_last_join);
            var lastSeen = calculateDateTime(player.stats_last_seen);
            var timeAgo = 0;
            var output;

            // see which is more current, join or seen time
            if (lastSeen >= lastJoin) {
                timeAgo = lastSeen;
            } else {
                timeAgo = lastJoin;
            }

            //  check that the time isn't crazy, if so we just gonna "error" it out
            if (timeAgo > Date.now() || timeAgo === undefined) {
                output = <p className='center-align green lighten-3'>Never Seen</p>
            } else {
                output = <p className='center-align green lighten-3'>Seen {moment(timeAgo).fromNow()}</p>
            }

            return (
                <ReactCSSTransitionGroup transitionName="example" component='div' transitionAppear={true} transitionAppearTimeout={500} transitionEnterTimeout={500} transitionLeaveTimeout={500}>
                    <div className='col s6 m3 l2'>
                        <div className='card player-card'>
                            <h4 className='center-align green lighten-1'>{player.name}</h4>
                            <p className='center-align green lighten-2'>Play Time: {formattedTime}</p>
                            {output}
                            <div className='img-container'>
                                <img src={'https://minotar.net/body/'+player.name+'/100'}/>
                            </div>
                            <Link className='btn brown lighten-2 center-align' to={'/stats/player/' + player.name}>View Stats</Link>
                        </div>
                    </div>
                </ReactCSSTransitionGroup>
            );
        });

        return (
            <ReactCSSTransitionGroup transitionName="example" component='div' transitionAppear={true} transitionAppearTimeout={500} transitionEnterTimeout={500} transitionLeaveTimeout={500}>
                <div className='container'>
                    <div className='row'>
                        {playerNodes}
                    </div>
                </div>
                <Helmet
                    title="Players"
                    meta={[
                        {"name": "description", "content": "Moosecraft Player Base"},
                        {"property": "og:title", "content": "Moosecraft - Players"},
                        {"property": "og:description", "content": "Moosecraft Player Base"},
                        {"property": "og:image", "content": "//static.moosecraft.us/img/tile.png"}
                    ]}
                />
            </ReactCSSTransitionGroup>
        )
    }
}

export default StatsPlayerIndex;



