import React from 'react'
import moment from 'moment'
import ellipsize from 'ellipsize'
import { Link } from 'react-router'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'

class Article extends React.Component{
    constructor(props) {
        super(props)
    }

    render() {
        var truncatedText = ellipsize(this.props.article.body, 250);

        return (
            <ReactCSSTransitionGroup transitionName="example" transitionAppear={true} transitionAppearTimeout={500} transitionEnterTimeout={500} transitionLeaveTimeout={500}>
                <div className='row'>
                    <div className='col s12 m12'>
                        <div className='card darken-1'>
                            <div className='card-action green lighten-1'>
                                <span className='card-title black-text'>
                                    {this.props.article.title} - {moment(this.props.article.created).fromNow()}
                                </span>
                            </div>
                            <div className='card-content'dangerouslySetInnerHTML={{ __html: truncatedText }}></div>
                            <div className='card-action'>
                                <Link to={'/articles/' + this.props.article.slug} className='btn brown lighten-2'>Continue Reading</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </ReactCSSTransitionGroup>
        );
    }
}

export default Article;



