import React from 'react';
import axios from 'axios';

class HungerBar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            hunger: undefined
        }
    }

    componentDidMount() {
        // get player hunger
        axios.get('//api.moosecraft.us/api/players/hunger/' + this.props.username)
            .then( (response) =>  {
                if (response.data['result'] === "success") {
                    this.setState({
                        hunger: response.data['hunger']
                    });
                } else {
                    console.error("Unable to fetch player hunger: " + response.data['error']);
                }                
            })
            .catch((error) => console.log(error));
    }

    render() {
        if (this.state.hunger != undefined) {
            var numHunger = Math.floor(this.state.hunger / 2);
            var elements = [];
            for (var i = 0; i < numHunger; i++) {
                elements.push('hunger');
            }

            // account for half full hunger
            if ((this.state.hunger % 2) != 0) {
                elements.push('half-hunger');
            }

            // get the array length, make it go to 10
            for (var j = elements.length; j < 10; j++) {
                elements.push('hunger-empty');
            }

            var hungerNodes = elements.map(function(node) {
                return (
                    <img src={'//static.moosecraft.us/img/' + node + '.png'}/>
                )
            });
        }

        return (
            <div className='hunger-bar'>
                {hungerNodes}
            </div>
        )
    }

}

export default HungerBar;

        


