import React from 'react'

class SliderElement extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <li>
                <img src={"//static.moosecraft.us/img/" + this.props.element.img} />
                <div className={"caption " + this.props.element.align + "-align"}>
                    <h3>{this.props.element.caption}</h3>
                    <h5 className="light grey-text text-lighten-3">{this.props.element.small_caption}</h5>
                </div>
            </li>
        );
    }
}

export default SliderElement;



