import React from 'react'
import {Link} from 'react-router'

class Footer extends React.Component{
    constructor(props) {
        super(props);
    }
    
    render() {
        return (
            <footer className='page-footer brown lighten-1'>
                <div className='container'>
                    <div className='row'>
                        <div className='col s12 m6 l3'>
                            <h5 className='white-text'>Support Moosecraft</h5>
                            <p className='grey-text text-lighten-4'>Hosting this server isn't cheap.  Help keep us up and running!</p>
                            <form action="https://www.paypal.com/cgi-bin/webscr" method="post" target="_top">
                                <input type="hidden" name="cmd" value="_s-xclick" />
                                <input type="hidden" name="hosted_button_id" value="XB9NGAP5CWAMW" />
                                <button className='btn waves-effect waves-light green' type='submit' name='submit' alt="PayPal - The safer, easier way to pay online!">Donate Now</button>
                                <img alt="" src="https://www.paypalobjects.com/en_US/i/scr/pixel.gif" width="1" height="1" />
                            </form>
                        </div>
                        <div className='col s12 m6 l3'>
                            <h5 className='white-text'>Moosecraft Hosting provided by Mooseman Studios</h5>
                            <p className='grey-text text-lighten-4'>Moosecraft is proud to be hosted by Mooseman Studios.  Please check out <a href='http://moosemanstudios.com' className='grey-text text-lighten-3'>Mooseman Studios</a> for more information.</p>
                        </div>
                        <div className='col s12 m6 l3'>
                            <h5 className='white-text'>Support/Help</h5>
                            <p className='grey-text text-lighten-4'>For support or help please click the following link:</p>
                            <Link to={'/support'} className='btn waves-effect waves-light green'>Support / Help</Link>
                        </div>
                        <div className='col s12 m6 l3'>
                            <h5 className='white-text'>Keep up with the latest information. Follow Us:</h5>
                            <ul className='social_media_icons'>
                                <li><a href='https://www.facebook.com/MoosemanStudios'><img src="//static.moosecraft.us/img/social/Facebook.png" alt=""/></a></li>
                                <li><a href='https://twitter.com/moosemanstudios'><img src="//static.moosecraft.us/img/social/Twitter.png" alt=""/></a></li>
                                <li><a href='https://plus.google.com/106226721854133951147'><img src="//static.moosecraft.us/img/social/GooglePlus.png" alt=""/></a></li>
                                <li><a href='http://api.moosecraft.us/api/articles/feed'><img src='//static.moosecraft.us/img/social/Rss.png' alt="RSS Feed"></img></a></li>
                            </ul>
                        </div>

                    </div>
                </div>
                <div className='footer-copyright'>
                    <div className='container'>
                        &copy; copyright 2016 Mooseman Studios
                    </div>
                </div>
            </footer>
        );
    }
}

export default Footer;



