import React from 'react'
import OnlinePlayer from './OnlinePlayer'

class OnlinePlayersList extends React.Component{
    constructor(props) {
        super(props);
    }

    render() {
        var playerNodes = this.props.players.map(function(player) {
            return (
                <OnlinePlayer username={player} key={player}/>
            );
        });
        return (
            <div className="row" id="players">
                {playerNodes}
            </div>
        );
    }
}

export default OnlinePlayersList;



