import React from 'react'
import Helmet from 'react-helmet'
import { Link } from 'react-router'

class MapIndex extends React.Component{
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className='container'>
                <iframe id='dynmap' className='hide-on-small-only' src='//dynmap.moosecraft.us'></iframe>

                <div className='row hide-on-med-and-up'>
                    <div className='col s12 m12'>
                        <div className='card darken-1'>
                            <div className='card-content'>
                                <span className='card-title black-text'>Live Map</span>
                                <p className='flow-text'>Due to the nature of the live map we have elected to hide this on smaller devices.  The viewing experience is highly obscured to the point of being useless.</p>
                                <p className='flow-text'>If you would like to view this still you can view it full screen below.</p>
                            </div>
                            <div className='card-action'>
                                <Link to ="//dynmap.moosecraft.us">View Map</Link>
                                <Link to="/">Return Home</Link>
                            </div>
                        </div>
                    </div>
                </div>

                <Helmet
                    title="Live Map"
                    meta={[
                        {"name": "description", "content": "Moosecraft Live World Map"},
                        {"property": "og:title", "content": "Moosecraft - Live Map"},
                        {"property": "og:description", "content": "Moosecraft Live World Map"},
                        {"property": "og:image", "content": "//static.moosecraft.us/img/tile.png"}
                    ]}
                />
            </div>
        )
    }

    set_dynmap() {
        $('#dynmap').parent().addClass('dynmap-iframe');

        // get window height
        var window_height = $(window).height();
        var footer_height = $('footer').height();
        var topnav_height = $('.top-nav').height();

        $('#dynmap').css('height', window_height - footer_height - topnav_height - 63);
    }

    componentDidMount() {
        this.set_dynmap();

        // window resize on dynmap page
        $(window).resize(function() {
            this.set_dynmap();
        });
    }
}

export default MapIndex;



