import React from 'react'
import * as firebase from 'firebase'
import {Link} from 'react-router'
import Helmet from 'react-helmet'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'
import Constants from '../../../Constants'

import ServerRulesList from './ServerRulesList.js'

class RulesIndex extends React.Component{
    constructor(props) {
        super(props)
        this.state = {
            rules: []
        }
    }

    componentWillMount() {
        var app = firebase.initializeApp(Constants.config).database().ref('categories');
        app.on('child_added', (dataSnapshot) => {
            // retrieve the old state
            var list = this.state.rules;
            list.push(dataSnapshot.val());
            this.setState({
                rules: list
            });
        });
    }

    componentWillUnmount() {
        //app.off();
    }

    render() {
        var column1 = [];
        var column2 = [];

        for (var i = 0; i < this.state.rules.length; i++) {
            if (i % 2 == 0) {
                column1.push(this.state.rules[i]);
            } else {
                column2.push(this.state.rules[i]);
            }
        }

        var column1Nodes = column1.map(function(category) {
            return (
                <ReactCSSTransitionGroup transitionName="example" component='li' transitionAppear={true} transitionAppearTimeout={500} transitionEnterTimeout={500} transitionLeaveTimeout={500}>
                    <div className='card darken-1'>
                        <div className='card-action green lighten-1'>
                            <span className='card-title darken-2'>{category['name']}</span>
                        </div>
                        <div className='card-content rules-collapsible'>
                            <ServerRulesList rules={category['rules']} key={category['name']}/>
                        </div>
                    </div>
                </ReactCSSTransitionGroup>
            )
        });

        var column2Nodes = column2.map(function(category) {
            return (
                <ReactCSSTransitionGroup transitionName="example" component='li' transitionAppear={true} transitionAppearTimeout={500} transitionEnterTimeout={500} transitionLeaveTimeout={500}>
                    <div className='card darken-1'>
                        <div className='card-action green lighten-1'>
                            <span className='card-title darken-2'>{category['name']}</span>
                        </div>
                        <div className='card-content rules-collapsible'>
                            <ServerRulesList rules={category['rules']} key={category['name']}/>
                        </div>
                    </div>
                </ReactCSSTransitionGroup>
            )
        });

        return (
            <div className='container'>
                <div className='row'>
                    <div className='col s12 m12 l6'>
                        {column1Nodes}
                    </div>
                    <div className='col s12 m12 l6'>
                        {column2Nodes}

                        <ReactCSSTransitionGroup transitionName="example" component='li' transitionAppear={true} transitionAppearTimeout={500} transitionEnterTimeout={500} transitionLeaveTimeout={500}>
                            <div className='card darken-1'>
                                <div className='card-action green lighten-1'>
                                    <span className='card-title darken-2'>Join Now!</span>
                                </div>
                                <div className='card-content rules-collapsible'>
                                    <ul className='collapsible'>
                                        <li>
                                            <div className='collapsible-header'><span className='bold'>Submit an application to play today! <Link className='btn brown lighten-2 waves-effect waves-brown' to={'/join'}>Click Here</Link></span></div>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </ReactCSSTransitionGroup>
                    </div>
                </div>

                <Helmet
                    title="Server Rules"
                    meta={[
                        {"name": "description", "content": "Moosecraft Server Rules"},
                        {"property": "og:title", "content": "Moosecraft - Server Rules"},
                        {"property": "og:description", "content": "Moosecraft Server Rules"},
                        {"property": "og:image", "content": "//static.moosecraft.us/img/tile.png"}
                    ]}
                />
            </div>
        )
    }
}

export default RulesIndex;



