import React from 'react'
import {Link} from 'react-router'

class OnlinePlayer extends React.Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        $('.tooltipped').tooltip({delay: 50});
    }

    render() {
        return (
            <Link to={'/stats/player/' + this.props.username}>
                <img className='tooltipped' data-position='bottom' data-delay='50' data-tooltip={this.props.username} src={"https://minotar.net/avatar/" + this.props.username + "/30"}/>
            </Link>
        )
    }
}

export default OnlinePlayer;



