import React from 'react';
import Helmet from 'react-helmet';

import Header from './header/Header.js';
import Footer from './footer/Footer.js';
import ChatWidget from '../components/Widgets/ChatWidget/ChatWidget.js';
import ScrollTopWidget from '../components/Widgets/ScrollTopWidget/ScrollTopWidget.js';
import { requestPermission } from '../components/Notifications.js';

//const socket = io('ws.moosecraft.us');

class MainLayout extends React.Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        requestPermission();
        window.cookieconsent_options = {"message":"Moosecraft.us uses cookies to ensure you get the best experience on our website","dismiss":"Got it!","learnMore":"More info","link":null,"theme":"dark-floating"};
    }

    render() {
        return (
            <div className='content'>
                
                <Header/>

                <main>
                    {this.props.children}
                </main>

                <div className='fixed-action-btn horizontal'>
                    <ScrollTopWidget />
                    <ChatWidget />
                </div>
                
                <Footer/>

                <Helmet
                    htmlAttributes={{"lang": "en", "amp": undefined}}
                    title=""
                    titleTemplate="Moosecraft - %s"
                    defaultTitle=""
                    /*base={{"target": "_blank", "href": "https://moosecraft.us"}}*/
                    meta={[
                        {"name": "keywords", "content": "Minecraft, Moosecraft, Bukkit, Spigot, Survival, Role Play"},
                        {"name": "author", "content": "moose517"},
                        {"name": "theme-color", "content": "#4CAF50"},
                        {"name": "msapplication-config", "content": "//static.moosecraft.us/browserconfig.xml"},
                        {"name": "viewport", "content": "width=device-width, initial-scale=1, maximum-scale=1.0, user-scalable=no"},
                        {"http-equiv": "content-type", "content": "text/html; charset=UTF-8"},
                        {"http-equiv": "X-UA-Compatible", "content": "IE=edge"},
                        {"property": "og:site_name", "content": "Moosecraft.us"},
                        {"property": "og:url", "content": "https://moosecraft.us" + this.props.location.pathname}
                    ]}
                    link={[
                        {"rel": "apple-touch-icon", "href": "//static.moosecraft.us/img/apple-touch-icon.png"},
                        {"redl": "manifest", "href": "//static.moosecraft.us/manifest.json"},
                        {"rel": "shortcut icon", "href": "//static.moosecraft.us/img/favicon.ico"},
                        {"rel": "author", "href": "//static.moosecraft.us/humans.txt"},
                        {"rel": "stylesheet", "href": "//fonts.googleapis.com/icon?family=Material+Icons"},
                        {"rel": "stylesheet", "href": "//cdnjs.cloudflare.com/ajax/libs/materialize/0.97.8/css/materialize.min.css"},
                        {"rel": "stylesheet", "href": "//static.moosecraft.us/css/font-awesome-animation.min.css"},
                        {"rel": "stylesheet", "href": "//static.moosecraft.us/css/styles.css"}
                    ]}
                />
            </div>
        );
    }
}

export default MainLayout;



