import React from 'react'
import Helmet from 'react-helmet'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'

class AboutIndex extends React.Component {
    constructor(props) {
        super(props)
    }

    componentDidMount() {
        stButtons.locateElements();
    }

    render() {
        return (
            <ReactCSSTransitionGroup transitionName="example" component='div' transitionAppear={true} transitionAppearTimeout={500} transitionEnterTimeout={500} transitionLeaveTimeout={500}>
                <div className='container'>
                    <div className='row'>
                        <div className='col s12 m12 l8'>
                            <div className="card green lighten-2">
                                <div className="card-content">
                                    <span className="card-title">Who Are We?</span>
                                    <p>Moosecraft is a very player and community focused minecraft server.  We are here to have fun first and foremost and do what we can to ensure it stays that way.  Our great staff handles trouble makers quickly to make sure your time isn't ruined.  We also host contests to get the community involved!  Stop by and check us out anytime.</p>
                                </div>
                            </div>
                        </div>
                        <div className='col l3 offset-l1 hide-on-med-and-down'>
                            <img className='responsive-img about' src="http://placehold.it/274x196" />
                        </div>
                    </div>

                    <div className='row'>
                        <div className='col l3 hide-on-med-and-down'>
                            <img className='responsive-img about' src="http://placehold.it/274x196" />
                        </div>
                        <div className='col s12 m12 l8 offset-l1'>
                            <div className="card green lighten-2">
                                <div className="card-content">
                                    <span className="card-title">What Are We About?</span>
                                    <p>Here at Moosecraft we are proud to remain a more classic survival based server.  We find there is nothing better than putting in a little elbow grease to get what you want to build that amazing structure and feel the great accomplishment for doing so.  Wanna take on monsters, no problem we have you covered there as well.  Just wanna sit and chat with players, go right ahead!  We even have alternate sources to do so if you choose.</p>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className='row'>
                        <div className='col s12 m12 l8'>
                            <div className="card green lighten-2">
                                <div className="card-content">
                                    <span className="card-title">Where we are going</span>
                                    <p>With the release of 1.9.4 we decided to start fresh and load up a new world.  This isn't something we like to do but every once in a while it just needs to be done for the new content.  As such there are many large projects we'd love community involvement with and are always looking for people to help build.  Other coming additions include questing, leveling abilities, and other story aspects that again we'd love to have members take part in as well!  If you have any questions please ask an admin today!</p>
                                </div>
                            </div>
                        </div>
                        <div className='col s12 m12 l3 offset-l1'>
                            <img className='responsive-img about' src="http://placehold.it/274x218" />
                        </div>
                    </div>
                </div>
                <Helmet
                    title='About'
                    meta={[
                        {"name": "description", "content": "Learn all about Moosecraft Minecraft Server"},
                        {"property": "og:title", "content": "Moosecraft - About"},
                        {"property": "og:description", "content": "Learn all about Moosecraft Minecraft Server"},
                        {"property": "og:image", "content": "//static.moosecraft.us/img/logo.png"}
                    ]}
                />
            </ReactCSSTransitionGroup>
        )
    }
}

export default AboutIndex;



