import React from 'react'

class ChatWidget extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <a className="btn-floating btn-large green lighten-2">
                <i className="large material-icons">chat</i>
            </a>
        );
    }
}

export default ChatWidget;



