import React from 'react';
import axios from 'axios';

class HealthBar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            hearts: undefined
        }
    }

    componentDidMount() {
        // get player health
        axios.get('//api.moosecraft.us/api/players/health/' + this.props.username)
            .then( (response) =>  {
                if (response.data['result'] === 'success') {
                    this.setState({
                        hearts: response.data['health']
                    });
                } else {
                    console.error("Unable to fetch player health: " + response.data['error']);
                }
                
            })
            .catch((error) => console.log(error));
    }

    render() {
        if (this.state.hearts != undefined) {
            var numHearts = Math.floor(this.state.hearts / 2);
            var elements = [];
            for (var i = 0; i < numHearts; i++) {
                elements.push('heart');
            }

            // account for half full heart
            if ((this.state.hearts % 2) != 0) {
                elements.push('half-heart');
            }

            // get the array length, make it go to 10
            for (var j = elements.length; j < 10; j++) {
                elements.push('heart-empty');
            }

            var healthNodes = elements.map(function(node) {
                return (
                    <img src={'//static.moosecraft.us/img/' + node + '.png'}/>
                )
            });
        }

        return (
            <div className='health-bar'>
                {healthNodes}
            </div>
        )
    }

}

export default HealthBar;



