import React from 'react';

class ProgressBar extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        var percentage = (this.props.lower / this.props.upper) * 100;
        var divStylePositive = {
            width: percentage + '%'
        }
        var divStyleNegative = {
            width: 100 - percentage + '%'
        }

        return (
            <div className="progress-bar">
                <div className="bar positive" style={divStylePositive}>
                    <span>{this.props.text}</span>
                </div>
                <div className="bar negative" style={divStyleNegative}>
                    <span>{this.props.text}</span>
                    </div>
            </div>
        )
    }
}

export default ProgressBar;



