import React from 'react'
import axios from 'axios'
import moment from 'moment'
import ellipsize from 'ellipsize'
import Helmet from 'react-helmet'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'

class ArticleView extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            article: []
        }
    }

    componentDidMount() {
        axios.get('//api.moosecraft.us/api/articles/view/'+this.props.params.slug)
            .then( (response) =>  {
                this.setState({
                    article: response['data']
                })
                stButtons.locateElements();
            })
            .catch((error) => console.log(error));
    }

    render() {
        return (
            <ReactCSSTransitionGroup transitionName="example" component='div' transitionAppear={true} transitionAppearTimeout={500} transitionEnterTimeout={500} transitionLeaveTimeout={500}>
                <div className='container'>
                    <div className='row'>
                        <div className='col s12 m12'>
                            <div className='card darken-1'>
                                <div className='card-action green lighten-1'>
                                    <span className='card-title black-text'>
                                        {this.state.article.title} - {moment(this.state.article.created).fromNow()}
                                    </span>
                                </div>
                                <div className='card-content' dangerouslySetInnerHTML={{__html: this.state.article.body}}></div>
                                <div className='card-action'>
                                    <span className='st_facebook_large' displayText='Facebook'></span>
                                    <span className='st_twitter_large' displayText='Tweet'></span>
                                    <span className='st_googleplus_large' displayText='Google +'></span>
                                    <span className='st_reddit_large' displayText='Reddit'></span>
                                    <span className='st_stumbleupon_large' displayText='StumbleUpon'></span>
                                    <span className='st_tumblr_large' displayText='Tumblr'></span>
                                    <span className='st_pinterest_large' displayText='Pinterest'></span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <Helmet
                    title={this.state.article.title}
                    meta={[
                        {"name": "description", "content": ellipsize(this.state.article.body, 250)},
                        {"property": "og:title", "content": "Moosecraft - " + this.state.article.title},
                        {"property": "og:description", "content": ellipsize(this.state.article.body, 250)},
                        {"property": "og:image", "content": "//static.moosecraft.us/img/tile.png"}
                    ]}
                />
            </ReactCSSTransitionGroup>
        )
    }
}

export default ArticleView;



