import React from 'react'
import axios from 'axios'
import Helmet from 'react-helmet'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'

import ArticlesList from './ArticlesList.js'
import Slider from '../../../components/Widgets/Slider/slider.js'

class HomeIndex extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            articles: [],
            limit: 5,
            currentPage: 0
        }
    }

    componentDidMount() {
        axios.get('//api.moosecraft.us/api/articles?limit=' + this.state.limit + '&page=' + this.state.currentPage)
            .then( (response) =>
                this.setState({
                    articles: response['data']['data']
                })
            )
            .catch( (error) => console.log(error));
    }

    render() {
        return (
            <div className='container'>
                <ReactCSSTransitionGroup transitionName="example" transitionAppear={true} transitionEnterTimeout={500} transitionLeaveTimeout={500} transitionAppearTimeout={500}>
                    <div className='row'>
                        <div className='col s12 m12'>
                            <div className='card-panel white'>
                                <span className='black-text center-align flow-text'>Connect now: mc.moosecraft.us</span>
                            </div>
                        </div>
                    </div>
                </ReactCSSTransitionGroup>

                <ReactCSSTransitionGroup transitionName="example" transitionAppear={true} transitionEnterTimeout={500} transitionLeaveTimeout={500} transitionAppearTimeout={500}>
                    <Slider />
                </ReactCSSTransitionGroup>

                <ReactCSSTransitionGroup transitionName="example" transitionAppear={true} transitionEnterTimeout={500} transitionLeaveTimeout={500} transitionAppearTimeout={500}>
                    <ArticlesList articles={this.state.articles}/>
                </ReactCSSTransitionGroup>

                <Helmet
                    title="Home"
                    meta={[
                        {"name": "description", "content": "Moosecraft Home Page, latest news and more"},
                        {"property": "og:title", "content": "Moosecraft - Home"},
                        {"property": "og:description", "content": "Moosecraft Home Page, latest news and more"},
                        {"property": "og:image", "content": "//static.moosecraft.us/img/logo.png"}
                    ]}
                />
            </div>
        )
    }
}

export default HomeIndex;



