import React from 'react'
import { Link } from 'react-router'
import SubSideNavLinkList from './SubSideNavLinkList'

class SideNavLink extends React.Component{
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        $('.collapsible').collapsible();
    }

    render() {
        if (this.props.link.children) {
            return (
                <li className='bold'>
                    <a className='collapsible-header waves-effect waves-green faa-horizontal animated-hover'><i className={'fa fa-' + this.props.link.icon + ' fa-lg'}></i>{this.props.link.label}</a>
                    <div className='collapsible-body'>
                        <SubSideNavLinkList links={this.props.link.children} />
                    </div>
                </li>
            );
        } else {
            return (
                <li className='bold waves-effect waves-green faa-horizontal animated-hover'><Link data-activates='slide-out' to={this.props.link.location} activeClassName='active'><i className={'fa fa-' + this.props.link.icon + ' fa-lg'}></i>{this.props.link.label}</Link></li>
            );
        }
    }
}

export default SideNavLink;



