import React from 'react'
import axios from 'axios'
import Server from './Server'

class ServerStatusesWidget extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            moosecraft: 'grey',
            mojang: 'grey',
            interval: 60000
        }

        this.changeInterval = this.changeInterval.bind(this);
        this.loadServerList = this.loadServerList.bind(this);
    }

    render() {
        return (
            <li className='sidenav-section no-padding'>
                <ul id='status_ul'>
                    <Server status={this.state.moosecraft} server={"Moosecraft"} />
                    <Server status={this.state.mojang} server={"Mojang"} />
                </ul>
            </li>
        );
    }

    changeInterval(interval) {
        this.setState({
            interval: interval
        });
    }

    componentDidMount() {
        this.loadServerList();
        setInterval(this.loadServerList, this.state.interval);
    }

    componentWillUnmount() {
        this.serverRequest.abort();
        clearInterval(this.loadServerList);
    }

    loadServerList() {
        // fetch moosecraft status first
        axios.get('//api.moosecraft.us/api/server/status/moosecraft')
            .then((response) =>
                this.setState({
                    moosecraft: response['data']['status']
                })
            )
            .catch((error) => console.log(error));

        // mojang server status
        axios.get('//api.moosecraft.us/api/server/status/mojang')
            .then((response) =>
                this.setState({
                    mojang: response['data']['status']
                })
            )
            .catch((error) => console.log(error));
    }
}

export default ServerStatusesWidget;



