import React from 'react'
import SliderElement from './SliderElement.js'

class Slider extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            sliderElements: []
        }
    }

    componentDidMount() {
        // load elements here
        this.serverRequest = $.ajax({
            url: '//api.moosecraft.us/api/media/slider',
            dataType: 'json',
            cache: false,
            success: function(data) {
                this.setState({
                    sliderElements: data['slider']
                });

                $('.slider').slider({full_width: true});    // must be set here otherwise grey box
            }.bind(this),
            error: function(xhr, status, err) {
                console.error('//moosecraft.us/api/media/slider', status, err.toString());
            }.bind(this)
        });
    }

    componentWillUnmount() {
        this.serverRequest.abort();
    }

    render() {
        var sliderNodes = this.state.sliderElements.map(function(element) {
            return (
                <SliderElement element={element} key={element.caption} />
            );
        });
        return (
            <div className='slider'>
                <ul className='slides'>
                    {sliderNodes}
                </ul>
            </div>
        )
    }
}

export default Slider;



