import React from 'react'
import axios from 'axios'
import moment from 'moment'
import Helmet from 'react-helmet'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'

import HungerBar from './HungerBar'
import HealthBar from './HealthBar'
import Rank from './Rank'
import McMMO from './mcmmo'

class StatsPlayerView extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            player: [],
        }
    }

    componentWillReceiveProps(newProps) {
        this.loadPlayerData(newProps.params.username);
    }

    componentDidMount() {
        this.loadPlayerData(this.props.params.username);
    }

    render() {
        var lastSeen = calculateDateTime(this.state.player['stats_last_seen']);
        var lastJoin = calculateDateTime(this.state.player['stats_last_join']);

        return (
            <ReactCSSTransitionGroup transitionName="example" component='div' transitionAppear={true} transitionEnterTimeout={500} transitionLeaveTimeout={500}>
                <div className='container'>
                    <div className='row'>

                        <div className='col s12 m12 l4'>
                            <div className='card player-avatar'>
                                <div className='avatar'>
                                    <img src={'https://minotar.net/body/' + this.state.player['name'] + '/150'} />
                                </div>
                                <HealthBar username={this.props.params.username} />
                                <HungerBar username={this.props.params.username} />
                            </div>
                        </div>

                        <div className='col s12 m12 l8'>
                            <div className='card'>
                                <div className='card-action green lighten-1'>
                                    <span className='card-title black-text'>
                                        {this.state.player['name'] + "'s Stats"}
                                    </span>
                                </div>
                                <div className='card-content'>
                                    <ul>
                                        <Rank username={this.props.params.username} />
                                        <li className='flow-text'>Status: [online/offline]</li>
                                        <li className='flow-text'>Playtime: {calculatePlayTime(this.state.player['stats_playtime'])}</li>
                                        <li className='flow-text'>Last Joined: {moment(lastJoin).fromNow()}</li>
                                        <li className='flow-text'>Last Seen: {moment(lastSeen).fromNow()}</li>
                                        <li className='flow-text'>Time Joined Server: {getCount(this.state.player['stats_join'])}</li>
                                        <li className='flow-text'>Distance Traveled: {Math.floor(getCount(this.state.player['stats_move']))}</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className='row'>
                        <div className='col s12 m12 l8'>
                            <div className='card'>
                                <div className='card-content'>
                                    <table className='centered striped flow-text'>
                                        <tbody>
                                            <tr>
                                                <td>Experience Gained</td>
                                                <td>{getCount(this.state.player['stats_xp_gained'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Words Said</td>
                                                <td>{getCount(this.state.player['stats_words_said'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Server Votes</td>
                                                <td>{getCount(this.state.player['stats_vote'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Villager Trades</td>
                                                <td>{getCount(this.state.player['stats_trades'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Tools Broken</td>
                                                <td>{getCount(this.state.player['stats_tools_broken'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Server Kicks</td>
                                                <td>{getCount(this.state.player['stats_times_kicked'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Worlds Changed</td>
                                                <td>{getCount(this.state.player['stats_times_changed_world'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Teleports</td>
                                                <td>{getCount(this.state.player['stats_teleports'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Sheep Sheered</td>
                                                <td>{getCount(this.state.player['stats_shears'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Blocks Broken</td>
                                                <td>{getCount(this.state.player['stats_blocks_broken'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Blocks Placed</td>
                                                <td>{getCount(this.state.player['stats_blocks_placed'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Top PVP Streak</td>
                                                <td>{getCount(this.state.player['stats_pvp_top_streak'])}</td>
                                            </tr>
                                            <tr>
                                                <td>PVP Streak</td>
                                                <td>{getCount(this.state.player['stats_pvp_streak'])}</td>
                                            </tr>
                                            <tr>
                                                <td>PVP</td>
                                                <td>{getCount(this.state.player['stats_pvp'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Doors Locked</td>
                                                <td>{getCount(this.state.player['stats_lock'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Food Eaten</td>
                                                <td>{getCount(this.state.player['stats_omnomnom'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Mob Killed</td>
                                                <td>{getCount(this.state.player['stats_kill'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Items Picked Up</td>
                                                <td>{getCount(this.state.player['stats_items_picked_up'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Items Dropped</td>
                                                <td>{getCount(this.state.player['stats_items_dropped'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Items Crafted</td>
                                                <td>{getCount(this.state.player['stats_items_crafted'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Fish Caught</td>
                                                <td>{getCount(this.state.player['stats_fish_caught'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Eggs Thrown</td>
                                                <td>{getCount(this.state.player['stats_eggs_thrown'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Deaths</td>
                                                <td>{getCount(this.state.player['stats_death'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Damage Taken</td>
                                                <td>{getCount(this.state.player['stats_damage_taken'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Commands Executed</td>
                                                <td>{getCount(this.state.player['stats_commands_done'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Buckets Filled</td>
                                                <td>{getCount(this.state.player['stats_buckets_filled'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Buckets emptied</td>
                                                <td>{getCount(this.state.player['stats_buckets_emptied'])}</td>
                                            </tr>
                                            <tr>
                                                <td>Arrows Shot</td>
                                                <td>{getCount(this.state.player['stats_arrows'])}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>

                        <McMMO username={this.props.params.username} />

                    </div>

                </div>

                <Helmet
                    title={this.state.player['name'] + "'s Stats"}
                    meta={[
                        {"name": "description", "content": this.state.player['name'] + "'s Stats"},
                        {"property": "og:title", "content": "Moosecraft - " + this.state.player['name'] + "'s Stats"},
                        {"property": "og:description", "content": this.state.player['name'] + "'s Stats"},
                        {"property": "og:image", "content": "//static.moosecraft.us/img/tile.png"}
                    ]}
                />

            </ReactCSSTransitionGroup>
        )
    }

    loadPlayerData(username) {
        axios.get('//api.moosecraft.us/api/players/view/' + username)
        .then( (response) => {
            this.setState({
                player: response.data['player'][0]
            });
        })
        .catch( (error) => console.log(error));
    }
}

function getCount(stat) {
    var count = 0;
    if (Array.isArray(stat)) {
        for (var item in stat) {
            count += stat[item]['value'];
        }
    } else {
        for (var key in stat) {
            if (key == "value") {
                count = stat[key];
            }
        }
    }
    return count;
}

function calculateDateTime(time) {
    for (var key in time) {
        return time[key]['value'];
    }
}

function calculatePlayTime(playertime) {
    var total_time = 0;

    // loop to get Playtime
    for (var key in playertime) {
        total_time = total_time + playertime[key]['value'];
    }

    if (total_time != 0) {
        var outputString = '';

        // calculate our how many days, hours, minutes player has played
        var days = Math.floor(total_time/60/60/24);
        total_time = total_time - (days*60*60*24);

        if (days != 0) {
            outputString += days + 'days ';
        }

        var hours = Math.floor(total_time/60/60);
        total_time = total_time - (hours*60*60);

        if ((days == 0 && hours != 0) || days != 0) {
            outputString += hours + 'hours ';
        }

        var minutes = Math.floor(total_time/60);
        var seconds = total_time - (minutes*60);
        outputString += minutes + 'minutes ' + seconds + 'seconds';
    } else {
        outputString = '-- N/A --'
    }

    return outputString;
}

export default StatsPlayerView;



