import React from 'react';
import Router from 'react-router';
import { Provider } from 'react-redux';
import { createStore } from 'redux';
import {Route, browserHistory, IndexRoute, NotFoundRoute } from 'react-router';

import moosecraftApp from './reducers'

import MapPage from './pages/Map/Index/Index.js'
import HomePage from './pages/Home/Index/Index.js'
import NotFound from './pages/NotFound/NotFound.js'
import RulesPage from './pages/Rules/Index/Index.js'
import MainLayout from '../src/layout/MainLayout.js'
import AboutPage from './pages/About/Index/Index.js'
import ArticleViewPage from './pages/Articles/View/View.js'
import StatsPlayerPage from './pages/Stats/Player/Index/Index.js'
import StatsPlayerDetailPage from './pages/Stats/Player/View/View.js'

let store = createStore(moosecraftApp); //, window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__());

export default (
    <Provider store={store}>
        <Route path='/' component={MainLayout}>
            <IndexRoute component={HomePage}/>
            <Route path='articles/:slug' component={ArticleViewPage}/>
            <Route path='stats/players' component={StatsPlayerPage}/>
            <Route path='stats/player/:username' component={StatsPlayerDetailPage}/>
            <Route path='rules' component={RulesPage}/>
            <Route path='map' component={MapPage}/>
            <Route path='about' component={AboutPage}/>
            <Route path='*' component={NotFound}/>
        </Route>
    </Provider>
);



