import React from 'react'
import axios from 'axios'

class McMMO extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            McmmoStats: []
        }
    }

    componentDidMount() {
        axios.get('//api.moosecraft.us/api/players/mcmmo/' + this.props.username)
        .then( (response) => {
            this.setState({
                McmmoStats: response.data['stats'][0]['mcmmo_skill']
            });
        })
        .catch( (error) => console.log(error) );
    }

    render() {
        var state = this.state.McmmoStats;
        var powerLevel = 0;

        Object.keys(state).forEach(function (key) {
           powerLevel += state[key]
        });

        return (
            <div className='col s12 m12 l4'>
                <div className='card'>
                    <div className='card-action green lighten-1'>
                        <span className='card-title black-text'>McMMO Levels</span>
                    </div>
                    <div className='card-content'>
                        <table className='flow-text centered striped'>
                            <tbody>
                                <tr><td>Power Level</td><td>{powerLevel}</td></tr>
                                {Object.keys(state).map(function(key) {
                                    if (key != 'user_id')
                                        return <tr><td>{key}</td><td>{state[key]}</td></tr>;
                                })}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        )
    }
}

export default McMMO;


