import React from 'react'
import axios from 'axios'
import { connect } from 'react-redux'

import ProgressBar from '../../progress_bar.js'
import OnlinePlayersList from './OnlinePlayersList'
import { spawnNotification } from '../../Notifications'
import { addOnlinePlayer } from '../../../actions';

class OnlinePlayersWidget extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            online: 0,
            max: 0,
            players: []
        }
    }

    render() {
        return (
            <li className='sidenav-section no-padding'>
                <ul id='status_ul'>
                    <ProgressBar text={'Players Online ' + this.state.online + '/' + this.state.max} lower={this.state.online} upper={this.state.max} />
                    <OnlinePlayersList players={this.state.players} />
                </ul>
            </li>
        );
    
    }

    componentDidMount() {
        // on first load lets grab the list of players and online from the api end point
        this.loadPlayerList();
        //setInterval(this.loadPlayerList, 2000);

        // establish websocket connection now, this is how we will get data from now out
        try {
            var webSocket = new WebSocket('wss://ws.moosecraft.us');

            // send the subscribe
            webSocket.onopen = function(evt) {
                console.log("WebSocket Connection open...");
                var data =  {
                    "name": "connections",
                    "key": "7a0b946c7e10df11a18aaebdb3ff3f187efaac8b1c65e918593b14a62319f33e",
                    "username": "admin",
                    "arguments": [],
                    "tag": "sampleTag"
                };
                webSocket.send('/api/2/subscribe?json=' + encodeURIComponent(JSON.stringify(data)));
            };

            // take action on messages for join quit
            webSocket.onmessage = function(event) {
                //console.log('Recieved message: ' + event.data);
                var data = JSON.parse(event.data);  // convert json back to array

                if (data['result'] == "success") {

                    // stream connection and disconnection
                    if (data['success']['action'] == 'disconnected') {
                        // get the state, find the element that disconnected and remove it
                        // send the resulting array back
                        var players = this.state.players;
                        var index = players.indexOf(data['success']['player'])
                        players.splice(index, 1);

                        this.setState({
                            players: players,
                            online: this.state.online - 1
                        });

                        spawnNotification('Player Quit', data['success']['player'] + ' has left the game.', 'https://minotar.net/avatar/' + data['success']['player']);

                    } else if (data['success']['action'] == 'connected') {
                        var newArray = this.state.players;
                        newArray.push(data['success']['player']);

                        this.setState({
                            players: newArray,
                            online: this.state.online + 1
                        });

                        //dispatch(addOnlinePlayer(data['success']['player']));

                        spawnNotification('Player Join', data['success']['player'] + ' has joined the game.', 'https://minotar.net/avatar/' + data['success']['player']);
                    }
                }
            }.bind(this);

            // log to console when we close the socket
            webSocket.onclose = function(evt) {
                console.log("WebSocket Connection closed...");
            };
        } catch (exception) {
            console.log(exception);
            this.setState({
                players: [],
                online: 0
            });
        }

    }

    componentWillUnmount() {
        this.webSocket.close();
    }

    loadPlayerList() {
        axios.get('//api.moosecraft.us/api/server/online')
            .then((response) =>
                this.setState({
                    online: response['data']['online'],
                    max: response['data']['max'],
                    players: response['data']['players']
                })
            )
            .catch((error) => console.log(error));

    }
}

export default OnlinePlayersWidget;//connect()(OnlinePlayersWidget);



