import React from 'react';
import axios from 'axios';

class Rank extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            rank: 'unknown'
        }
    }

    componentDidMount() {
        // get player health
        axios.get('//api.moosecraft.us/api/players/rank/' + this.props.username)
            .then( (response) =>  {
                if (response.data['result'] === 'success') {
                    this.setState({
                        rank: response.data['groups'][0]['primary_group']
                    });
                } else {
                    console.error("Unable to fetch player rank: " + response.data['error']);
                }
                
            })
            .catch((error) => console.log(error));
    }

    render() {
        var output;
        switch(this.state.rank) {
            case 'guest': {
                output = "Pauper";
                break;
            }
            case 'builder': {
                output = "Land Owner";
                break;
            }
            case 'advbuilder': {
                output = "Merchant";
                break;
            }
            case 'masterbuilder': {
                output = "Knight";
                break;
            }
            case 'mod': {
                output = "Baron";
                break;
            }
            case 'admin': {
                output = "Magister";
                break;
            }
            case 'superadmin': {
                output = "Warlord Veteran";
                break;
            }
            case 'owner': {
                output = "Overlord";
                break;
            }
            default: {
                output = "Pauper";
                break;
            }

        }

        return (
            <li className='flow-text'>Rank: {output}</li>
        )
    }

}

export default Rank;



