import React from 'react'

class TopNav extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <nav className='top-nav green'>
                <div className='container'>
                    <div className='nav-wrapper'>
                        <a className='page-title'>Moosecraft</a>
                    </div>
                </div>
            </nav>
        );
    }
}

export default TopNav;



