import React from 'react'
import Helmet from 'react-helmet'

class NotFound extends React.Component{
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className='container'>
                <div className='row'>
                    <div className='col s12 m12'>
                        <div className='card-panel white'>
                            <span className='black-text center-align flow-text'>404 Not Found!</span>
                            <p className='flow-text'>The page you are looking for cannot be found.  Oooops</p>
                        </div>
                    </div>
                </div>
                <Helmet title="404"/>
            </div>
        )
    }
}

export default NotFound;



