import React from 'react'

import ServerRule from './ServerRule.js'

class ServerRulesList extends React.Component{
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        $('.collapsible').collapsible();
    }

    render() {
        var ruleNodes = this.props.rules.map(function(rule) {
            return (
                <ServerRule name={rule['name']} description={rule['description']} created={rule['created']} key={rule['name']} />
            )
        });

        return (
            <ul className='collapsible' data-collapsible='accordian'>
                {ruleNodes}
            </ul>
        )
    }
}

export default ServerRulesList;



