const players = (state = [], action) => {
    switch(action.type) {
        case 'PLAYER_CONNECTED': {
            return [
                ...state, 
                {
                name: action.name
                }
            ]
        }
        case 'PLAYER_DISCONNECTED': {
            return {
                name: action.name
            }
        }
        default:
            return state
    }
}

export default players;


