import React from 'react'
import { Link } from 'react-router'

class SubSideNavLink extends React.Component{
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <li className='sideNavSubLink'><Link to={this.props.link.location} activeClassName='active'>{this.props.link.label}</Link></li>
        );
    }
}

export default SubSideNavLink;



